// Change Dimension Property Name of Dimension field.
jQuery(document).ready(function ($) {
  //     // $("[name='btss_meta[bts_column][left]']").attr('placeholder', 'Mobile')
  $("[name='_bp3dimages_[angle_property][top]']").attr("placeholder", "X");
  $("[name='_bp3dimages_[angle_property][right]']").attr("placeholder", "Y");
  $("[name='_bp3dimages_[angle_property][bottom]']").attr("placeholder", "Z");
});

document.addEventListener("DOMContentLoaded", function () {
  // custom css
  const subtitleElement = document.querySelector(".custom-css .csf-subtitle-text");
  if (subtitleElement) {
    subtitleElement.innerText = `#bp_model_id_${parseInt(window.post_ID?.value)}`;
  }

  // hide woocommerce admin notice
  setTimeout(() => {

    const dismissEl = document.querySelector(".bp3dv_woocommerce_admin_notice .notice-dismiss");

    dismissEl?.addEventListener("click", () => {
      fetch(ajaxurl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: new URLSearchParams({
          action: 'dismiss_product_edit_notice',
          security: document.querySelector('.bp3dv_woocommerce_admin_notice')?.dataset?.nonce,
        })
      })
        .then(response => response.json())
        .then(data => {
          if (data.success) {
            console.log(data);

            console.log('Notice dismissed for 30 days');
          }
        })
        .catch(error => console.error('Error:', error));

    });
  }, 500);
});
